<template>
    <section id="main-content" class="main-content">
        <!-- start page heading -->
        <section class="main-content">
            <div class="container-fluid padding-t_10">
                <!-- start left panel -->
                <div class="left-menu-tab " id="ClientLeftMenu">
                    <leftsearch @SetLeftSearch="SetLeftSearch" @click="ResetButton" classs="DepartmentchkItems"
                        header="Manage Client Storage" v-bind:fields="leftSearchfeild" modulename="DocStorage"></leftsearch>
                </div>

                <div class="right-content" id="ClientRightContent">
                    <div class="col-md-12 pb-0">
                        <!-- <div class="row">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                <div class="progress storagegraph" style="height:2rem;">
                                    <div class="progress-bar progress-bar-primary progress-bar-striped px-2 text-right pb-style" id="divConsumendStorage"><span>x%</span></div>
                                </div>
                            </div>
                        </div> -->
                        <div class="row">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                <StorageBar :type="'Doc'"></StorageBar>
                            </div>
                        </div>
                    </div>
                    <hr class="mt-2 mb-2 p-0" />
                    <div class="col-md-12 p-0" v-for="(item,index) in userStorageDetailSummary" :key="index">
                        <!--dashboard-->
                        <ul class="dashbord-widget">
                            <li class="one_wid">
                                <h3><span id="spnStorageLimit" class="inherit-span">{{
                                    item.DOC_STORAGE_MAXLIMIT
                                }}</span> MB<br><span>Total</span></h3>
                                <em class="fa fa-hdd-o" aria-hidden="true"></em>
                            </li>
                            <li class="two_wid">
                                <h3><span id="TotalConsumedSpace" class="inherit-span">{{
                                    totalConsumedStorage
                                }}</span>MB<br><span>Consumed</span></h3>
                                <em class="fa fa-hdd-o" aria-hidden="true"></em>
                            </li>
                            <li class="three_wid">
                                <h3><span id="TotalAvailableSpace" class="inherit-span">{{
                                     item.DOC_STORAGE_MAXLIMIT-(AllowedClientStorage + totalUserAllowedStorage)
                                }}</span>MB<br><span>Available</span></h3>
                                <em class="fa fa-hdd-o" aria-hidden="true"></em>
                            </li>
                            <li class="four_wid">
                                <h3><span id="AssignedtoUser" class="inherit-span">{{ totalUserAllowedStorage }}</span>
                                    MB<br><span>Assigned To User</span></h3>
                                <em class="fa fa-user-o" aria-hidden="true"></em>
                            </li>
                            <li class="five_wid">
                                <h3><span class="inherit-span" id="abc">{{ AllowedClientStorage }}</span>
                                    MB<br><span>Assigned To Client</span>
                                </h3>
                                <em class="fa fa-id-card-o" aria-hidden="true"></em>
                            </li>
                        </ul>
                        <!--dashboard-->
                        <div class="clearfix"></div>
                    </div>
                    <div class="col-md-12 p-0">
                        <div class="theme-primary partition p-actions-expand">
                            <span class="p-actions float-right">
                                <a id="btnStorageSpace" class="p-action-btn text-white disabled" href="#myModal"
                                    @click="AllocatedSpacePopup" data-toggle="modal" alt="Allocate Space"
                                    title="Allocate Space">
                                    <em class="fa fa-plus"></em><br />Allocate Space
                                </a>
                                <input id="ReportViewer" type="hidden" value="L" />
                            </span>
                            <span class="collapse-head-btns">
                                <a href="javascript:;" class="collapse-btns"><em class="fa fa-angle-up text-white"></em></a>
                            </span>
                        </div>
                    </div>
                    <div class="col-md-12 p-0">
                        <div class="listing mt-2">
                            <div class="expand-list">
                                <a href="javascript:;" @click="ExpandCollapseList"
                                    class="expand-list-btn justify-content-center align-items-center">
                                    <em class="icon-solid arrow-solid-left"></em>
                                </a>
                            </div>

                            <!-- Listing record of user storage -->
                            <div class="listing">
                                <div class="table-responsive table-fix-header" id="div_forListView">
                                    <table class="table table-bordered mt-0" style="width: 100%;" id="tblFileList">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th style="width:50px !important;" class="text-center"><input id="chkAll"
                                                        class="chkitems" value="" type="checkbox" v-on:change="onChange()">
                                                </th>
                                                <th>
                                                    <span>{{$t("ClientName")}}</span>
                                                </th>

                                                <th>
                                                    <span>{{$t("Email")}}</span>
                                                </th>
                                                <th>
                                                    <span>{{$t("Status")}}</span>
                                                </th>
                                                <th>
                                                    <span>Consumed Storage (MB)</span>
                                                </th>
                                                <th>
                                                    <span>Allocated Storage (MB)</span>
                                                </th>
                                                <th data-width="5%" class="text-center nonresize">
                                                    <span>Action</span>
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item,index1) in UserData" :key="index1">
                                                <td class="text-center"><input :Id="item.USER_ID"
                                                        v-bind:value="item.USER_ID" class="chkItems" type="checkbox"
                                                        v-on:change="onChange()"></td>
                                                <td>
                                                    <span class="text-overflow-dynamic-container">
                                                        {{ item.USER_NAME }}
                                                    </span>
                                                </td>

                                                <td>
                                                    <span class="text-overflow-dynamic-container">
                                                        {{ item.EMAIL_ID }}
                                                    </span>
                                                </td>
                                                <td>
                                                     <a class="inactive-disabled">
                                                    <span
                                                        class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-success"
                                                        v-if="item.STATUS_ID == '1001'"> Active</span>

                                                    <span title=""
                                                        class="sent bg-secondary btn d-inline-block w-80 text-center text-truncate text-white"
                                                        v-if="item.STATUS_ID == '1084'"> Approved</span>

                                                    <span title=""
                                                        class="stop btn d-inline-block w-80 text-center text-truncate btn-danger"
                                                        v-if="item.STATUS_ID == 'Rejected'"> Rejected</span>

                                                    <span title=""
                                                        class="stop px-2 bg-primary btn d-inline-block w-80 text-center text-truncate"
                                                        v-if="item.STATUS_ID == '1003'"> Deleted</span>

                                                    <span
                                                        class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-danger"
                                                        v-if="item.STATUS_ID == '1002'">Inactive</span>

                                                    <span
                                                        class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-secondary"
                                                        v-if="item.STATUS_ID == '1005'"> Pending</span>

                                                    <span
                                                        class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-warning"
                                                        v-if="item.STATUS_ID == 'ActivationAwaited'">
                                                        ActivationAwaited</span>
                                                     </a>
                                                </td>
                                                <td>
                                                    <span class="text-right text-overflow-dynamic-container">
                                                        {{ item.CONSUMED_STROAGE }}
                                                    </span>
                                                </td>
                                                <td>
                                                    <span class="text-overflow-dynamic-container">
                                                        <input type="text"
                                                            :id="'idMaxStorage' + item.USER_ID" step="0.01" maxlength="13" disabled="disabled"
                                                            v-model="item.ALLOWED_STORAGE"
                                                            class="maxAllowedStorage  form-control text-right" />
                                                        <!-- :value="item.ALLOWED_STORAGE" -->
                                                    </span>
                                                     
                                                </td>
                                                <td class="text-center single-action">

                                                    <a class="aEdit" title="Edit" v-if="hideEditIcon"
                                                        :id="'idEdit' + item.USER_ID"
                                                        @click="editAllowedStorage(item.USER_ID)"><em
                                                            class="fa fa-pencil text-success action-icon "></em></a>
                                                    <div :id="'divAction' + item.USER_ID" style="display: none;">

                                                        <a class="aCancel mr-2" :id="'idCancel_' + item.USER_ID"
                                                            :value="item.USER_ID" title="Cancel"
                                                            @click="CancelAction(item.USER_ID, item.ALLOWED_STORAGE)"
                                                            href="javascript:;"><em
                                                                class="fa fa-times-circle text-danger action-icon"></em></a>
                                                        <a class="aSave" title="Save" :value="item.USER_ID"
                                                            :id="'idSave_' + item.USER_ID" href="javascript:;"
                                                            @click="SaveData(item.USER_ID, item.ALLOWED_STORAGE)"><em
                                                                class="fa fa-save text-primary action-icon"></em></a>
                                                    </div>
                                                </td>
                                            </tr>
                                            <tr v-if="UserData.length == 0">
                                                <td colspan="7" class="text-danger text-center no-record">{{ $t('NoRecordfound') }}
                                                </td>
                                            </tr>
                                            <tr v-if="NoRecordLength > 0">
                                                <td colspan="2" class="text-right fr font-weight-bold">Total</td>
                                                <td colspan="4" class="text-right fr font-weight-bold">{{
                                                    totalConsumedStorage }} MB </td>
                                                <td class=" text-right fr font-weight-bold">{{ totalAllowedStorage }} MB
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <AllocatedSpace :jsonobj="jsonobj" v-if="isAllocatedSpaceOpen"></AllocatedSpace>
        </section>
    </section>
</template>

<script>

import Pager from '../../components/Common/Pager.vue';
import DataService from '../../services/DataService';
import AllocatedSpace from '../UI/Modal/ModalAllocatedSpace.vue'
import leftsearch from "../../components/Common/left-search.vue"
import StorageBar from '../../components/Common/StorageBar.vue'
import schema from "../UI/ClientStorageLeftSearchSchema"
export default {

    components: {
        Pager,
        AllocatedSpace,
        StorageBar,
        leftsearch
    },

    data() {
        return {

            isLoading: false,
            UserData: [],
            userStorageDetailSummary: [],
            pageNo: 1,
            searchCondition: null,
            sortExp: null,
            sortBy: null,
            pageSize: 10,
            totalRecords: '',
            dropDivShow: false,
            showByNameDiv: false,
            showByConsumedDiv: false,
            divDesignationShow: false,
            hideEditIcon: true,
            isAllocatedSpaceOpen: false,
            UserStorageValue: null,
            UserId: null,
            saveStorage: null,
            StorageValue: null,
            jsonobj: [],
            JSONSearchData: [],
            departmentList: null,
            designationList: null,
            txtSearchUserName: "",
            txtConsumedStorage: "",
            userFilterData: [],
            CheckIfExist: [],
            jsonFilteredData: [],
            label: false,
            totalConsumedStorage: 0,
            totalAllowedStorage: 0,
            NoRecordLength: 0,
            DepartmentDataTemp:[],
            CheckIfExistStatus:[],
            AllowedClientStorage: null,
            totalUserAllowedStorage: null,
            leftSearchfeild: schema.lefrsearchschema,
            userAssignedStorage: null,
            tempUserData: [],
            jsonObjData:[],
            statusSelectedArray:[]
        }
    },

    created() {
        var vm = this;
         vm.FetchData();
         vm.FetchUserData();
         schema.bindleftCommonSearchdropdown(vm,"DocStorage","Status");
    },
    methods:
    {
        SearchUserStorage: function () {
            var vm = this;
            vm.userFilterData = [];
            vm.JSONSearchData=[];
            if ($("input[name=UserName]").val() != "") {
                if (vm.userFilterData.length == 0) {
                    vm.jsonFilteredData = vm.UserData;
                }
                vm.CheckIfExist = vm.UserData.filter(usr => usr.USER_NAME.includes($("input[name=UserName]").val().trim()));
                vm.JSONSearchData = Array.from(vm.CheckIfExist);
                vm.userFilterData = vm.userFilterData.concat(vm.JSONSearchData)
            }
            if ($("input[name=Email]").val() != "") {
                var email=$("input[name=Email]").val();
                if (vm.userFilterData.length == 0) {
                    vm.jsonFilteredData = vm.UserData;
                }
                 vm.CheckIfExist = vm.jsonFilteredData.filter(function (el) {
                    return el.EMAIL_ID ==email.trim();
                });
                vm.JSONSearchData = Array.from(vm.CheckIfExist);
                vm.userFilterData = vm.userFilterData.concat(vm.JSONSearchData)
            }
            if ($("input[name=ConsumedStorageFrom]").val() != "") {
                if (vm.userFilterData.length == 0) {
                    vm.jsonFilteredData = vm.UserData;
                }
                vm.CheckIfExist = vm.jsonFilteredData.filter(function (el) {
                    if(parseInt($("input[name=ConsumedStorageFrom]").val())<=el.USED_STORAGE && parseInt($("input[name=ConsumedStorageTo]").val())>=el.USED_STORAGE)
                    {
                        return el.USED_STORAGE;
                    }
                    
                });
                vm.JSONSearchData = Array.from(vm.CheckIfExist);
                vm.userFilterData = vm.userFilterData.concat(vm.JSONSearchData)
            }
           
            $('input[name=Status]:checked').each(function () {
                if (vm.userFilterData.length == 0) {
                    vm.jsonFilteredData = vm.UserData;
                }
                var designId = $(this).val();
                vm.CheckIfExistStatus = vm.UserData.filter(desig => desig.STATUS_ID == designId);
                vm.JSONSearchData = Array.from(vm.CheckIfExistStatus);
                vm.userFilterData = vm.userFilterData.concat(vm.JSONSearchData)
                if (vm.statusSelectedArray.length > 0) {
                    vm.divDesignationShow = true;
                }
                else {
                    vm.divDesignationShow = false;
                }

            });
            if ($("input[name=ConsumedStorageFrom]").val()=="" && $("input[name=ConsumedStorageTo]").val()==""  && $("input[name=UserName]").val() == "" && $("input[name=Email]").val() == "" && $('input[name=Status]:checked').length==0) {
                vm.UserData = vm.tempUserData;
            }
            else {
                vm.UserData = vm.userFilterData;
            }
            setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
            vm.getTotalStorageData();
        },
        ExpandCollapseList() {
            var element1 = document.getElementById("ClientLeftMenu");
            element1.classList.toggle("left-menu-tab-collapsed");
            var element2 = document.getElementById("ClientRightContent");
            element2.classList.toggle("right-content-expand");
        },
        ResetButton: function () {
            var vm = this;
            vm.FetchData();
            vm.dropDivShow = false;
            vm.divDesignationShow = false;
            vm.showByNameDiv = false;
            vm.showByConsumedDiv = false;
            $('.chkItems').prop("checked", false);
            vm.txtSearchUserName = '';
            vm.txtConsumedStorage = '';
            vm.toggleExpandCollapseButton(vm.label = true);
            vm.SetLeftSearch();
        },

        toggleExpandCollapseButton: function () {
            var vm = this;
            if (vm.label == false) {
                vm.dropDivShow = true;
                vm.divDesignationShow = true;
                vm.showByNameDiv = true;
                vm.showByConsumedDiv = true;
                vm.label = true;
            }
            else {
                vm.dropDivShow = false;
                vm.divDesignationShow = false;
                vm.showByNameDiv = false;
                vm.showByConsumedDiv = false;
                vm.label = false;
            }
           
        },
    
        getTotalStorageData: function () {
            var vm = this;
            vm.totalAllowedStorage = 0;
            vm.totalConsumedStorage = 0;
            vm.UserData.forEach(x => {
                vm.totalAllowedStorage += x.ALLOWED_STORAGE;
                vm.totalConsumedStorage += x.CONSUMED_STROAGE;
            });
            vm.avaliablestorage = parseInt(vm.totalAllowedStorage) - parseInt(vm.totalConsumedStorage);

        },
        SetLeftSearch: function () {
            var vm = this;
            vm.UserData = vm.tempUserData;
            vm.SearchUserStorage();
        },
        AllocatedSpacePopup: function () {
            var vm = this;
            vm.jsonobj = [];
            var values = ''
            $('#tblFileList .chkItems:checked').not("[id^='chkAll']").each(function () {
                values = $(this).val();
                let objData = {
                    Id: values
                }
                vm.jsonobj.push(objData)
            })
            vm.isAllocatedSpaceOpen = true;
        },

        closeAllocatedSpace: function () {
            var vm = this;
            vm.isAllocatedSpaceOpen = false;
        },

        // Save data on save icon 
        SaveData: function (id, allowed,consumed) {
            var vm = this
            vm.isLoading = true;
            vm.jsonObjData=[];
            vm.UserId = id,
                vm.UserStorageValue = allowed;
            let objData = {

                DataId: vm.UserId,
                DataValue: vm.UserStorageValue
            }
            vm.jsonObjData.push(objData)
            let userSetting=[{
                    refCode: 'UserSetting',
                    moduleName: 'DocStorage',
                }]
                var configData="configData"
             var conditionData="conditionData"
            var params="[{"+JSON.stringify(configData)+":"+JSON.stringify(vm.jsonObjData)+", "+JSON.stringify(conditionData)+":"+JSON.stringify(userSetting)+"}]"
              
           if(allowed>vm.total){
             vm.ShowAlert(vm.$t("Allocate Storage cannot be more than total Storage"), "failure", true, vm.$t('Alert'));
              vm.isLoading = false;
             return false;
           }
            if(allowed<consumed){
                vm.ShowAlert(vm.$t("Allocate Storage cannot be less than consumed Storage"), "failure", true, vm.$t('Alert'));
            }
            else if(isNaN(allowed)){
                vm.ShowAlert(vm.$t("Allocate Storage must be numeric."), "failure", true, vm.$t('Alert'));
            }
         else{
            
            DataService.ManageDocUserStorageSettingData(JSON.stringify(params)).then((response) => {

                if (response.data == 0) {
                    this.ShowAlert("Storage Configuration Data Updated Successfully")
                    $('#idMaxStorage' + id).attr('disabled','disabled');
                    $('#divAction' + id).hide();
                    $('#idEdit' + id).show();
                    vm.FetchData();
                    vm.$parent.GetStorageSetupStatusDetail();
                }
                else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }
            })
        }
            vm.isLoading = false;
        },

        CancelAction: function (id) {
            var vm = this;
            $('#idMaxStorage' + id).attr('disabled',true);
            $('#idEdit' + id).show();
            $('#divAction' + id).hide();
            vm.FetchData();
        },

        editAllowedStorage: function (id) {
            var vm = this;
            $('#idMaxStorage' + id).removeAttr('disabled');
            $('#idEdit' + id).hide();
            $('#divAction' + id).show();

        },
        onChange: function () {
            var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
            if (checkCount > 0) {
                $('#btnStorageSpace').removeClass('disabled');
            }
            else {
                $('#btnStorageSpace').addClass('disabled');
            }
        },
        FetchUserData: function () {
            // debugger
            var vm = this;
            vm.isLoading = true;
            var params = `pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&sortBy=&sortExp=&userTypeIds=1,2,3`;
            DataService.GetUserStorageDetail(params).then(function (response) {

                vm.userAssignedStorage = JSON.parse(response.data).USERSTORAGEDETAILLIST;
                vm.userAssignedStorage.forEach(x => {
                    vm.totalUserAllowedStorage += x.ALLOWED_STORAGE;
                    vm.totalUserConsumedStorage += x.CONSUMED_STROAGE;
                });
            })
            setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
            $('#btnStorageSpace').addClass('disabled')

        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            vm.UserData=[];
            var params = `pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&sortBy=&sortExp=&userTypeIds=4`;
            DataService.GetUserStorageDetail(params).then(function (response) {

                 JSON.parse(response.data).USERSTORAGEDETAILLIST.forEach(x => {
                        if(x.STATUS_ID==1001){
                                vm.UserData.push(x);
                                vm.tempUserData.push(x);
                        }
                })
                vm.totalRecords = vm.UserData[0].totalRecords
                vm.userStorageDetailSummary = JSON.parse(response.data).USERSTORAGEDETAILSUMMARY;
                vm.UserData.forEach(x => {
                    if (x.ALLOWED_STORAGE == null || x.ALLOWED_STORAGE == undefined) {
                        x.ALLOWED_STORAGE = 0;
                        vm.AllowedClientStorage += x.ALLOWED_STORAGE;
                    }
                    else {
                        vm.AllowedClientStorage += x.ALLOWED_STORAGE;
                    }
                    setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
                })

                // Calculation the sum using forEach
                vm.getTotalStorageData();
                vm.isLoading = false;
                if (vm.UserData == null) {
                    vm.NoRecordLength = 0;
                }
                else {
                    vm.NoRecordLength = vm.UserData.length;
                }
            })

            $('#btnStorageSpace').addClass('disabled')

        },
    }
}

</script>

<style>.scrolll {
    height: 300px;
    overflow: scroll;
}</style>